import {
  VuexModule,
  Module,
  getModule,
  Mutation,
  Action,
} from "vuex-module-decorators";
import store from "@/store";
import { Login, Profile, ProfileResponse, User, UserResponse } from "@/store/models";
import { loadProfile, login } from "@/store/api";

@Module({
  namespaced: true,
  name: "users",
  store,
  dynamic: true,
})
class UserModule extends VuexModule {
  user: User | null = null;
  profile: Profile | null = null;

  @Mutation
  setUser(userResponse: UserResponse) {
    this.user = userResponse.user;
  }
  @Mutation
  setProfile(profileResponse: ProfileResponse) {
    this.profile = profileResponse.profile;
  }
  get username() {
    return (this.user && this.user.username) || null;
  }

  @Action({ commit: "setUser" })
  async login(req: Login) {
    try {
      return await login(req);
    } catch (error) {
      console.error(error);
      throw new Error("Invaid Username or Password!")
    }
  }
  @Action({ commit: "setProfile" })
  async loadProfile(username: string) {
    try {
      return await loadProfile(username);
    } catch (error) {
      console.error(error);
    }
  }
}

export default getModule(UserModule);
